<template>
  <div>
    <div id="box1" class="box">
      <ul>
        <li>
          <el-input v-model="input1" placeholder="活动名称"></el-input>
        </li>
    <li>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
    </li>
        <li>
          <div class="block">
            <el-date-picker v-model="value1" type="date" placeholder="选择日期"></el-date-picker>
          </div>
        </li>

        <li>
          <el-button type="primary">查询</el-button>
        </li>
        <li>
          <el-button>重置</el-button>
        </li>
      </ul>
    </div>
    <div id="box2" class="box">
      <div id="table">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="活动ID" width="120" prop="classId"></el-table-column>
          <el-table-column prop="className" label="活动名称" width="140"></el-table-column>
          <el-table-column prop="classPic" label="活动时间" show-overflow-tooltip width="230"></el-table-column>
          <el-table-column prop="classTime" label="活动状态" show-overflow-tooltip></el-table-column>
          <el-table-column prop="classOnce" label="开团人数" show-overflow-tooltip></el-table-column>
                    <el-table-column fixed="right" label="操作" width="120">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="deleteRow(scope.$index, tableData)"
                type="text"
                size="small"
              >移除</el-button>
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
        <el-button type="text" size="small">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div id="page">
          <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "cont1",
  data() {
    return {
      input1: "",
        input2: "",
  options: [
        {
          value: "选项1",
          label: "未开始"
        },
        {
          value: "选项2",
          label: "进行中"
        },
        {
          value: "选项3",
          label: "已结束"
        },
  ],
      tableData: [
        {
          classId: "55544",
          className: "名称",
          classPic: "2019-09-09 12:09:09 至2019-09-09 12:09:09",
          classTime: "未开始",
          classOnce: "10",
        },             {
          classId: "55544",
          className: "名称",
          classPic: "2019-09-09 12:09:09 至2019-09-09 12:09:09",
          classTime: "未开始",
          classOnce: "10",
        },       {
          classId: "55544",
          className: "名称",
          classPic: "2019-09-09 12:09:09 至2019-09-09 12:09:09",
          classTime: "未开始",
          classOnce: "10",
        },       {
          classId: "55544",
          className: "名称",
          classPic: "2019-09-09 12:09:09 至2019-09-09 12:09:09",
          classTime: "未开始",
          classOnce: "10",
        },       {
          classId: "55544",
          className: "名称",
          classPic: "2019-09-09 12:09:09 至2019-09-09 12:09:09",
          classTime: "未开始",
          classOnce: "10",
        },  
         
      ],

      value: "",
      value1:"",
      multipleSelection: []
    };
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },   deleteRow(index, rows) {
      rows.splice(index, 1);
    },    handleClick(row) {
        console.log(row);
      }
  }
};
</script>
<style>
.box {
  padding: 20px 10px;
  width: 90%;
  margin: 20px auto;
  background: white;
  border-radius: 5px;
}
#box1 {
}
#box1 ul li {
  margin-left: 20px;
  list-style: none;
  display: inline-block;
}
#box2::after {
  display: block;
  content: "";
  clear: both;
}
#page {
  margin-top: 20px;
  float: right;
}
</style>